<template>
  <div class="form__counter" :class="{ product__counter: $route.name === 'cart' }">
    <button
      type="button"
      aria-label="Убрать один товар"
      @click.prevent="changeItemAmount(itemAmount - 1)"
    >
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <input type="text" v-model.number="itemAmountValue" />

    <button
      type="button"
      aria-label="Добавить один товар"
      @click.prevent="changeItemAmount(itemAmount + 1)"
    >
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>
export default {
  props: ['itemAmount'],
  model: {
    prop: 'itemAmount',
    event: 'changeItemAmount'
  },
  methods: {
    changeItemAmount(itemAmount) {
      if (itemAmount >= 1) this.$emit('changeItemAmount', itemAmount);
    }
  },
  computed: {
    itemAmountValue: {
      get() {
        return this.itemAmount;
      },
      set(value) {
        this.changeItemAmount(value);
      }
    }
  }
};
</script>
